import React, { useContext, useState } from 'react'
import { Waypoint } from 'react-waypoint'

import Container from '../components/Container'
import Box from '../components/Box'
import Button from '../components/Button'
import Text from '../components/Text'
// import BackgroundImage from '../components/BackgroundImage'

import useResponsive from '../contexts/mediaQuery/useResponsive'
import LayoutContext from '../layoutContext'

import SideInfo from './SideInfo'
import Sections from './Sections'
import Fund from './Fund'

const scrollToName = () => {
  document.getElementById('fund-head').scrollIntoView({ behavior: 'smooth' });
}

const HomePage = () => {
  const { isMobile } = useResponsive()
  const { fullpageHeight } = useContext(LayoutContext)
  const [showSide, setShowSide] = useState(true)
  return (
    <>
      <Box position="relative" height={fullpageHeight} bg="lightGray">
        <Box position="absolute" top={['35.34%', null, '26%', '53.5%']} left="50%" width={['81.33%', null, '58.33%']} transform="translateX(-50%)">
          <Text.h1 textAlign="center" color="primary" fontSize={['2.14rem', null, '2.86rem']} whiteSpace={['pre-wrap', null, 'nowrap']}>{'\n'}</Text.h1>
          <Text mt={['1.714em', null, '1em']} fontSize={['1.14em', null, '2em']}></Text>
          <Box mt="1.14em" textAlign="center">
            <Button fontSize={['1.14em', null, '1.86em']} onClick={scrollToName}></Button>
          </Box>
        </Box>
      </Box>
      <Box bg="bgGreen" pt={['5.286em', null, '8em']} pb={['3.57em', null, '5.714em']} id="fund-head">
        <Container fontSize={['2em', null, '2.86em']} color="white">
          <Text textAlign={['center', null, 'left']} fontWeight="bold">()</Text>
          <Text mt={['2rem', null, '2.57rem']}>()</Text>
        </Container>
      </Box>
      <Sections isMobile={isMobile} />
      <Waypoint onEnter={() => setShowSide(false)} onLeave={({ currentPosition }) => currentPosition === 'below' && setShowSide(true)} />
      <Fund />
      <Container>
        <Box mt="3.57em">
          <Text fontWeight="bold" fontSize={['1.43em', null, '2em']} color="prussianBlue"></Text>
        </Box>
        <Box mt="2em" height={600} bg="lightGray" />
        <Box textAlign="center" my="2em">
          <Button.transparent m="0.5em">DM</Button.transparent>
          <Button.transparent m="0.5em"></Button.transparent>
        </Box>
      </Container>
      {showSide && !isMobile && <SideInfo />}
    </>
  )
}

export default HomePage
