import React, { useMemo } from 'react';

import Table from './Table'

const formatDate = d => {
  const [y, m] = d.split('/')
  return `20${y}\n${+m}`
}

const DividendsTable = ({ data, title, titleMobile, isMobile, bg }) => {
  const dividnesData = useMemo(() => {
    const sliced = data.slice(0, 12)
    if (isMobile) {
      return {
        th: [titleMobile, '\n\n%', '\n\n%', '\n\n%()'],
        tbody: sliced.map(d => [formatDate(d['']), d['(%)'], d['(%)'], d['()(%)']])
      }
    }
    return sliced.reverse().reduce((res, d, index) => {
      res.th[index + 1] = formatDate(d[''])
      res.tbody[0][index + 1] = d['(%)']
      res.tbody[1][index + 1] = d['(%)']
      res.tbody[2][index + 1] = d['()(%)']
      return res;
    }, { th: [title], tbody: [['%'], ['%'], ['%()']]})
  }, [isMobile, title, titleMobile, data])
  return (
    <Table nowData={dividnesData} bg={bg} />
  )
};

export default DividendsTable;
