import React from 'react'
import Container from '../../components/Container'
import Box from '../../components/Box'
import Flex from '../../components/Flex'
import Source from '../../components/Source'
import Image from '../../components/Image'
import Text from '../../components/Text'
import Title from '../../components/Title'
import useResponsive from '../../contexts/mediaQuery/useResponsive'

import care from './care.svg'
import rate from './rate.svg'
import mobileRate from './mobile-rate.svg'
import rise from './rise.svg'
import mobileRise from './mobile-rise.svg'
import chance from './chance.svg'
import mobileChance from './mobile-chance.svg'


const Risk = ({ innerRef }) => {
  const { isMobile, isLaptop } = useResponsive()
  const sets = [
    {
      title: '',
      imgs: [
        {
          chartTitle: '',
          src: isMobile ? mobileRate : rate,
        },
        {
          chartTitle: '2008',
          src: isMobile ? mobileRise : rise,
        },
      ],
      source: 'Lipper, USD, 2001/12/31~2020/3/31Bloomberg Barclays Multiverse ex Tr A+to B-TR USDHS&P 500 NRBloomberg Barclays U.S. Treasury TR2008/10/31'
    },
    {
      title: '',
      imgs: [
        {
          src: isMobile ? mobileChance : chance,
        },
      ],
      text: '5',
      source: 'BloombergICE BofAML20192019/12/31, Schroders'
    },
  ]

  return (
    <Box ref={innerRef}>
      <Title step="" src={care} title="" />
      <Container py={['2.86em', null, '7.143em']}>
        <Text fontSize={['1.43em', null, '2em']}>1/3</Text>
        <Box>
          {sets.map(({ title, imgs, text, source }, index) => (
            <Box pt="6.714em" key={index}>
              <Text fontWeight="bold" fontSize={['1.43em', null, '2em']} color="prussianBlue">{title}</Text>
              <Text fontSize={['1.285em', null, '1.714em']}>{text}</Text>
              <Flex mt={['1.57em', null, "3.57em"]} flexDirection={['column', null, 'row']}>
                {imgs.map(({ chartTitle, src }, k) => (
                  <Box pt={[k &&'3.57em', null, '0']} px="1em" width={1} key={k} borderRight={['none', null, (imgs.length > 1 && !k) && '0.5px solid gray']} borderLeft={['none', null, k && '0.5px solid gray']}>
                    <Text fontSize={['1.43em', null, '1.714em']} fontWeight="bold">{chartTitle}</Text>
                    <Box mt="1.43em">
                      <Image src={src} />
                    </Box>
                  </Box>
                ))}
              </Flex>
              <Source>{source}</Source>
            </Box>
          ))}

        </Box>
      </Container>
    </Box>
  )
}

export default Risk
