import React from 'react'
import Container from '../../components/Container'
import Box from '../../components/Box'
import Flex from '../../components/Flex'
import Button from '../../components/Button'
import Source from '../../components/Source'
import Image from '../../components/Image'
import Text from '../../components/Text'
import Title from '../../components/Title'
import useResponsive from '../../contexts/mediaQuery/useResponsive'

import trophy from './trophy.svg'
import health from './health.svg'
import unrecycle from './unrecycle.svg'
import phone from './phone.svg'
import coin from './coin.svg'
import chart from './chart.svg'
import mobileChart from './mobile-chart.svg'

const sets = [
  {
    name: '',
    src: health,
    text: ''
  },
  {
    name: '',
    src: unrecycle,
    text: ''
  },
  {
    name: '',
    src: phone,
    text: ''
  },
  {
    name: '',
    src: coin,
    text: ''
  },
]

const Quality = ({ innerRef }) => {
  const { isMobile, isLaptop } = useResponsive()
  return (
    <Box ref={innerRef}>
      <Title step="" src={trophy} title="" />
      <Container py={['2.86em', null, '7.143em']}>
        <Text fontSize={['1.43em', null, '2em']}>
          
        </Text>
        <Text mt="7.143rem" fontWeight="bold" color="prussianBlue" fontSize={['1.43em', null, '2em']}></Text>
        <Text mt="3.57rem" fontWeight="bold" fontSize={['1.285em', null, '1.714em']}></Text>
        <Box mt="1.43em">
         <Image src={isMobile ? mobileChart : chart} />
        </Box>
        <Source>
          Schroders2020/3/31
        </Source>
        <Text mt="5.714rem" fontWeight="bold" color="prussianBlue" fontSize={['1.43em', null, '2em']}></Text>
        <Text fontSize={['1.285em', null, '1.714em']}></Text>
        <Flex mt="3.57em" flexDirection={['column', null, 'row']}>
          {sets.map(({ name, src, text}, index) => (
            <Box key={index} width={[1, null, (1 / sets.length)]}>
              <Flex alignItems="center" justifyContent={['auto', null, 'center']}>
                {isMobile && (
                  <Box width="6.428em">
                    <Image src={src} />
                  </Box>
                )}
                <Box mx={['0.86em', null, '3em']} flex={1}>
                  <Text textAlign={['left', null, 'center']} fontSize={['1.43em', null, '1.714em']} fontWeight="bold">{name}</Text>
                  {isMobile && (
                    <Text textAlign="left" fontSize="1.14em">{text}</Text>
                  )}
                </Box>
              </Flex>
              {isMobile && (index !== sets.length - 1) && (
                <Box color="bgGreen" textAlign="center" fontSize="2.5em" fontWeight="bold" py="1.5rem">
                  +
                </Box>
              )}
            </Box>
          ))}
        </Flex>
        {!isMobile && (
          <>
            <Flex py="2.14em">
              {sets.map(({ src }, index) => (
                <Box px="3em" position="relative" width={1 / sets.length} key={index}>
                  <Box px="1em">
                    <Image src={src} />
                  </Box>
                  {index !== sets.length - 1 && (
                    <Box color="bgGreen" fontSize="4.5em" fontWeight="bold" position="absolute" top="50%" right="0" transform="translate(50%, -50%)">
                      +
                    </Box>
                  )}
                </Box>
              ))}
            </Flex>
            <Flex>
              {sets.map(({ text }, index) => (
                <Box mx="3em" width={1 / sets.length} key={index}>
                  <Text textAlign="left" fontSize="1.25em">{text}</Text>
                </Box>
              ))}
            </Flex>
          </>
        )}
        <Source>
          Schroders2020/3/31
        </Source>
      </Container>
    </Box>
  )
}

export default Quality
