import React from 'react'
import Container from '../../components/Container'
import Box from '../../components/Box'
import Source from '../../components/Source'
import Image from '../../components/Image'
import Text from '../../components/Text'
import Title from '../../components/Title'
import useResponsive from '../../contexts/mediaQuery/useResponsive'

import globel from './globel.svg'
import diversity from './diversity.svg'
import mobileDiversity from './mobile-diversity.svg'
import multiple from './multiple.svg'
import mobileMultiple from './mobile-multiple.svg'
import single from './single.svg'
import mobileSingle from './mobile-single.svg'


const Diversity = ({ innerRef }) => {
  const { isMobile, isLaptop } = useResponsive()
  const sets = [
    {
      title: '',
      src: isMobile ? mobileDiversity : diversity,
      source: 'Schroders2020/3/31'
    },
    {
      title: '',
      src: isMobile ? mobileMultiple : multiple,
      text: '',
      source: '2020/04/30'
    },
    {
      title: '',
      src: isMobile ? mobileSingle : single,
      text: '',
      source: '2020/04/30'
    },
  ]

  return (
    <Box ref={innerRef}>
      <Title step="" src={globel} title="" />
      <Container py={['2.86em', null, '7.143em']}>
        <Text fontSize={['1.43em', null, '2em']}>60</Text>
        <Box>
          {sets.map(({ title, src, text, source }, index) => (
            <Box pt="6.714em" key={index}>
              <Text fontWeight="bold" fontSize={['1.43em', null, '2em']} color="prussianBlue">{title}</Text>
              <Text fontSize={['1.285em', null, '1.714em']}>{text}</Text>
              <Box mt={['1.57em', null, "3.57em"]}>
                <Image src={src} />
              </Box>
              <Source>{source}</Source>
            </Box>
          ))}
        </Box>
      </Container>
    </Box>
  )
}

export default Diversity
